	export default {
		data() {
			
			const currentDay=this.getTime('day')
			const currenTime=this.getTime('time')
			return {
				
				islogin:false,
				images:[{src:'../../static/学生登录逻辑.jpg'},
						{src:'../../static/教师逻辑.jpg'},
						{src:'../../static/教师登录首页.jpg'},
						{src:'../../static/学生登录首页.jpg'}],
				indicator:true,
				autoplay:true,
				duration:2000,
				interval:2000,
				localname:'',
				sex:'man',
				youselect:['中国'],
				indexofA:0,
				time:currenTime,
				today:currentDay,
				items: [{
				                        value: 'USA',
				                        name: '美国'
				                    },
				                    {
				                        value: 'CHN',
				                        name: '中国',
				                        checked: 'true'
				                    },
				                    {
				                        value: 'BRA',
				                        name: '巴西'
				                    },
				                    {
				                        value: 'JPN',
				                        name: '日本'
				                    },
				                    {
				                        value: 'ENG',
				                        name: '英国'
				                    },
				                    {
				                        value: 'FRA',
				                        name: '法国'
				                    }
				                ]
				
			}
		},
		onPullDownRefresh() {
			uni.navigateTo({
				url:'../index/index'
			})
		},
		onShareAppMessage() {
			console.log('aaa')
		},
		onLoad() {	
			this.islogin=global.isLogin()&&true
			if(this.islogin){
				this.localname=global.isLogin().username
			}
			else{
				
			}
		},
	
		computed:{

			youselected:function(){
			return this.youselect.join(',')
			},
			startDate:function(){
				var today=this.getTime('day')
				const oldday=today.replace(/\d+/,2000)
				return oldday
			},
			endDate:function(){
				var today=this.getTime('day')
				var nextyear=2+today.slice(0,4)
				const newday=today.replace(/\d+/,nextyear)
				return newday
			}
		},
		methods: {
			gotologin(){
				console.log('zheng')
				uni.switchTab({
					url:'../loginindex'
				})
			},
			changePlay(){
				this.autoplay=!this.autoplay
			},
			changeDot(){
				this.indicator=!this.indicator
			},
			changeDu(e){
				this.duration=e.target.value
			},
			changeIn(e){
				this.interval=e.target.value
			},
			changeVa(e){
				this.sex=e.target.value
			},
			changeLi(e){
				this.youselect=e.target.value
			},
			changePickC(e){
				this.indexofA=e.target.value
			},
			changePickT(e){
				this.time=e.target.value
			},
			changePickD(e){
				this.today=e.target.value
			},
			getTime(typ){
				let time=new Date()
				if(typ==='day'){
					let Y=time.getFullYear()
					let M=time.getMonth()+1
					let D=time.getDate()
					// console.log(`${Y}-${M}-${D}`)
					return `${Y}-${M}-${D}`
				}
				else{
					let H=time.getHours()
					let M=time.getMinutes()
					// console.log(`${H}:${M}`)
					return `${H}:${M}`
				}
			},
	},
}
